<template>
  <div>
    <nav class="uk-navbar-container background-primary uk-light" uk-navbar>

        <div class="uk-navbar-left">
            <a href="#offcanvas-slide" class="uk-navbar-toggle" uk-navbar-toggle-icon uk-toggle></a>
            <div id="offcanvas-slide" uk-offcanvas>
                <div class="uk-offcanvas-bar">

                    <ul class="uk-nav uk-nav-default">
                      <li>
                        <li class="uk-nav-header">Guide</li>
                          <ul class="uk-nav uk-navbar-dropdown-nav">
                            <li class="uk-navbar-subtitle">Esport</li>
                              <li><a href="#">Créer son compte</a></li>
                              <li><a href="#">Les tournois</a></li>
                              <li><a href="#">Guide de démarrage</a></li>
                              <li class="uk-navbar-subtitle">Club</li>
                              <li><a href="#">Créer son Club</a></li>
                              <li><a href="#">Documentation</a></li>
                          </ul>
                      </li>
                      <li>
                        <li class="uk-nav-header">Rejoindre le projet</li>
                        <ul class="uk-nav uk-navbar-dropdown-nav">
                            <li class="uk-navbar-subtitle">Développeurs</li>
                            <li><a href="https://github.com/Nayir/FifaChallenge-Open-Source">Github</a></li>
                            <li><a href="#">Chat</a></li>
                            <li class="uk-navbar-subtitle">Gameurs passionés</li>
                            <li><a href="#">Groupe Facebook</a></li>
                        </ul>
                      </li>
                      <li><a title="Logout" v-on:click="signOut()">Se déconnecter</a></li>
                    </ul>

                </div>
            </div>
        </div>
        <div class="uk-navbar-left">
          <router-link class="uk-link-reset" :to="{ name: 'user.profile' }">
            <div class="uk-float-left">
              <img class="uk-border-circle" width="50" height="50" :src="user.photoURL">
            </div>
            <div class="uk-float-right profile-info">
              <span>{{ username }}</span><br />
              <span>0 FCP</span>
            </div>
          </router-link>
        </div>
        <div class="uk-navbar-center">

            <ul class="uk-navbar-nav play">
              <li class="uk-active">
                <a href="/play" class="uk-button uk-button-danger">
                  JOUER
                </a>
              </li>
            </ul>

        </div>

        <div class="uk-navbar-right">

            <ul class="uk-navbar-nav">
                <li>
                    <a href="#">Guide</a>
                    <div class="uk-navbar-dropdown">
                        <ul class="uk-nav uk-navbar-dropdown-nav">
                            <li class="uk-nav-header">Esport</li>
                            <li><a href="#">Créer son compte</a></li>
                            <li><a href="#">Les tournois</a></li>
                            <li><a href="#">Guide de démarrage</a></li>
                            <li class="uk-nav-header">Club</li>
                            <li><a href="#">Créer son Club</a></li>
                            <li><a href="#">Documentation</a></li>
                        </ul>
                    </div>
                </li>
                <li>
                    <a href="#">Rejoindre le projet</a>
                    <div class="uk-navbar-dropdown">
                        <ul class="uk-nav uk-navbar-dropdown-nav">
                            <li class="uk-nav-header">Développeurs</li>
                            <li><a href="https://github.com/Nayir/FifaChallenge-Open-Source">Github</a></li>
                            <li><a href="#">Chat</a></li>
                            <li class="uk-nav-header">Gameurs</li>
                            <li><a href="#">Groupe Facebook</a></li>
                        </ul>
                    </div>
                </li>
                <li><a title="Logout" v-on:click="signOut()">Se déconnecter</a></li>
            </ul>

        </div>

    </nav>
  </div>
</template>

<script>
import firebase from 'firebase'
export default {
  name: 'header',
  data () {
    return {
      user: [],
      profile: []
    }
  },
  computed: {
    username () {
      if (this.profile.username) {
        return this.profile.username
      } else {
        return this.user.displayName
      }
    }
  },
  mounted () {
    var self = this
    firebase.auth().onAuthStateChanged(function (user) {
      if (user) {
        self.user = user
        firebase.database().ref('/users/' + user.uid).once('value').then(function (snapshot) {
          self.profile = snapshot.val().profile
          self.showSaveButton = true
        })
      } else {
        // TODO Leave the page ?
      }
    })
  },
  methods: {
    signOut () {
      var self = this
      firebase.auth().signOut().then(function () {
        self.$router.push({name: 'user.login'})
      }, function (error) {
        console.log(error)
      })
    }
  }
}
</script>

<style scoped>
  .play li {
    width: 200px;
  }
  .play li a {
    font-size: 24px;
  }

  .profile-info span {
    margin-left: 20px;
  }
</style>



